import React, { Component } from 'react'
import { bindActionCreators } from 'redux'
import { connect } from 'react-redux'

import * as AppActions from 'common/actions'
import * as actions from './actions'
import * as constants from './constants'

import Modal from 'react-modal'
import ReposList from './components/ReposList'
import RepoHeader from './components/RepoHeader'
import throttle from 'throttle-debounce/throttle'

class Repos extends Component {
	constructor(props){
		super(props)
		this.getRepos = this.getRepos.bind(this)
		this.getNextRepos = this.getNextRepos.bind(this)
		this.setSelectedLanguage = this.setSelectedLanguage.bind(this)
		this.language = 'All'
	}

	componentDidMount(){
		this.props.actions.getLanguages();
		this.getRepos()
	}

	render(){
		let modal = null;
		let {repos} = this.props

		return (
			<div className="page">
				<div className="page_header">
					<RepoHeader languages={this.props.languages}
						count={repos.length} 
						setSelectedLanguage={this.setSelectedLanguage}
						getFilteredRepo={this.getRepos}
						rateLimit={this.props.rateLimit}/>
				</div>
				<div className="page_content">
					<ReposList getNextRepos={this.getNextRepos} repos={this.props.repos}/> 
				</div>
			</div>
			)
	}

	setSelectedLanguage(value){
		this.language = value ? value : 'all'
	}

	filterRepo(query){
		this.props.actions.getRepos(query)
	}

	getRepos(query){
		// let {since} = this.props
		AppActions.showLoader()
		this.props.actions.getRepos({query})
		AppActions.hideLoader();
	}

	getNextRepos(){
		let _props = this.props
		// if(this.props.nextLink){
		// 	throttle(300, function(){
				AppActions.showLoader();
				_props.actions.getNextRepos();
				AppActions.hideLoader();
		// 	})
		// }
	}
}

function mapStateToProps(state) {
	let _reposData = state['repos']
	return {
		repos: _reposData['repos'],
		rateLimit: _reposData['rateLimit'],
		languages: _reposData['languages'],
		nextLink: _reposData['nextLink']
	}
}

function mapDispatchToProps(dispatch){
	return {
		actions: bindActionCreators(actions, dispatch)
	}
}

export default connect(mapStateToProps, mapDispatchToProps)(Repos);




