import React, { Component, PropTypes } from 'react'
import InfiniteScroller from 'react-variable-height-infinite-scroller'

require('./reposList.scss')

class ReposList extends Component {
    constructor(props){
        super(props)
        this.renderItem = this.renderItem.bind(this)
    }

    render() {
        let that = this
        if(this.props.repos.length > 0){
            return (
                <ul className="repoList">
                    <InfiniteScroller
                        onScroll={this.handleScroll}
                      averageElementHeight={100} // this is a guess you make!
                      containerHeight={600}
                      renderRow={this.renderItem} // function to render a row
                      totalNumberOfRows={this.props.repos.length}/>   
                </ul>
            );    
        }
        else{
            return null
        }
        
    }

    renderItem(rowNo){
        let repo = this.props.repos[rowNo]
        const iconStyles = { marginRight: 24 };

        return(
            <li className="repoList_item">
                <a href={repo.owner.avatar_url}>
                    <img alt="@beetbox" className="avatar repoList_item_hanging_avatar" height="32" src={repo.owner.avatar_url} width="32"/>
                </a>
                <h3 className="repoList_name">
                    <a href={repo.html_url} target="_blank">
                        {repo.full_name}
                    </a>
                </h3>
                <div className="repo-list_description">
                    {repo.description}
                </div>
            </li>
        )
    }


};

export default ReposList;


