import React, { Component, PropTypes } from 'react'
import Select from 'react-select';
import Slider from 'material-ui/Slider';

require('./repoHeader.scss')

class RepoHeader extends Component {
	constructor(props){
		super(props);

		let _props = this.props

		this.state = {
			'language': '',
			'stars': 0,
			'rateLimit': _props.rateLimit,
		}

		this.handleLanguageChange = this.handleLanguageChange.bind(this);
		this.handleStarsChange = this.handleStarsChange.bind(this);
	}

	componentWillReceiveProps(nextProps){
		this.setState({
			rateLimit: nextProps.rateLimit
		})
	}

	render(){
		let {finished, all} = this.state.rateLimit
		return(
			<div className="repoHeader cf">
				<div className="autoComplete_container">
					<Select
					    name="language"
					    value={this.state.language}
					    options={this.props.languages}
					    onChange={this.handleLanguageChange}/>
				</div>
				<div className="stars_slider">
					<div className="title">
						<span className="helper_icon">

						</span>
						<span className="text">{`>= ${this.state.stars} Stars`}</span>
					</div>
					<Slider 
						name="stars"
						defaultValue={5000}
						description=""
						min={0}
						max={10000}
						name="starSlider"
						step={100}
						style={{
							paddingLeft: '5px',
    						paddingRight: '5px',
    						marginTop: '0px',
    						height: '100%',
    						marginBottom: '0px'
						}}
						value={this.state.stars}
						onChange={this.handleStarsChange}/>
					<div className="slider_markers">
						<div className="start">0</div>
						<div className="end">10000</div>
					</div>
				</div>
				<div className="rateLimit_info">
					<div className="title">
						<span className="helper_icon">

						</span>
						<span className="text">Rate Limit</span>
					</div>
					<div className="description">
						<span>{finished}</span> out of <span>{all}</span>
					</div>
				</div>
				<div className="header_search_info">
                    Showing <span className="count">{this.props.count}</span> repositories for <span className="language">{this.state.language ? this.state.language : 'all'}</span>
                </div>
			</div>
		)
	}

	handleStarsChange(e, value){
		let selectedLanguage = this.state.language;
		let urlParsedLanguage = selectedLanguage.toLowerCase();
		let newQuery = ''
		if(selectedLanguage){
			if(value > 0){
				newQuery = `language:${urlParsedLanguage}+stars:">=${value}"`	
			}
			else{
				newQuery = `language:${urlParsedLanguage}`
			}
			
		}
		else{
			newQuery = `stars:">=${value}"`
		}
		this.setState({
			stars: value
		}, () =>{this.props.getFilteredRepo(newQuery)})
	}

	handleLanguageChange(e){
		let selectedLanguage = e ? e.value : '';
		let urlParsedLanguage = selectedLanguage.toLowerCase();
		let newQuery = ''
		if(selectedLanguage){
			if(this.state.stars > 0)
				newQuery = `language:${urlParsedLanguage}+stars:">=${this.state.stars}"`
			else{
				newQuery = `language:${urlParsedLanguage}`
			}
		}
		else{
			newQuery = `stars:">=${this.state.stars}"`
		}
		this.props.setSelectedLanguage(selectedLanguage)
		this.setState({
			language: selectedLanguage
		}, () => {this.props.getFilteredRepo(newQuery)})
	}

}

export default RepoHeader;


