import { handleActions } from 'redux-actions'

import * as constants from './constants';

const defaultState = {
	repos: [],
	languages: [],
	rateLimit: {
		'finished': 0,
		'all': 60
	},
	nextLink: ''
}

function getNextLink(link){
	let relNextIndex = link.indexOf('rel="next"');
	if(relNextIndex > -1){
		let closeBrIndex = link.indexOf(">")
		let nextLink = link.substr(1, closeBrIndex -1);
		return nextLink
	}
	return ''
}

export default handleActions(Object.assign({}, {
	
	[constants.GET_REPOS] (state, action){
		let newState = Object.assign({}, state);
		let payload = action.payload
		newState.repos = payload.body.items
		let headers = payload.header
		let rateLimit = headers['x-ratelimit-limit']
		let limitRemaining = headers['x-ratelimit-remaining']
		newState.rateLimit = {
			'finished': rateLimit - limitRemaining,
			'all': rateLimit
		}
		let link = headers['link']
		if(link){
			newState.nextLink = getNextLink(headers['link']);
		}
		else 
			newState.nextLink = ''
		return newState
	},

	[constants.GET_LANGUAGES] (state, action){
		let newState = Object.assign({}, state);
		let languages = action.payload
		let _temp = [];
		for(let language of languages){
			_temp.push({
				value: language,
				label: language
			})
		}
		newState.languages = _temp
		return newState;
	}

}), defaultState);


