import { createAction } from 'redux-actions';
import request from 'superagent'

import * as constants from './constants';

const searchAPI = 'https://api.github.com/search/repositories'
const languagesAPI = 'https://gist.githubusercontent.com/mayurah/5a4d45d12615d52afc4d1c126e04c796/raw/ccbba9bb09312ae66cf85b037bafc670356cf2c9/languages.json'

// https://api.github.com/search/repositories?q=language:javascript+stars:500

export const getRepos = createAction(constants.GET_REPOS, payload => new Promise((resolve, reject) => {
	let qsearch = ''
	let {query} = payload
	if(query){
		qsearch = `q=${query}`
	}
	else{
		qsearch = `q=stars:">=0"`
	}
	request.get(searchAPI)
		.query(qsearch) // check for request params
		.end((err, res) => {
			if (err) reject(err);

			if(res.body && res.statusCode == 200){
				let result = res;
				resolve(result);
			} else {
				reject(res.body);
			}
		})
}))


export const getLanguages = createAction(constants.GET_LANGUAGES, payload => new Promise((resolve, reject) => {

	request.get(languagesAPI)
		.end( (err, res) => {
			if(err) reject(err);
			resolve(JSON.parse(res.text));
		})
}))


